.selectize-input {
	@include select-style;

	padding: rem-calc(14 8);
	padding-left: rem-calc(14);
	background-color: transparent;
	border-radius: rem-calc(6);
	border-color: $bfLightGray;
	box-shadow: none;

	&::after {
		display: none !important;
	}

	&.full {
		background-color: transparent;
	}

	&.focus {
		box-shadow: none;
	}

	input {
		/* Remove First */
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;

		/* Then Style */
		background: transparent;
		outline: none;
	}
}

.selectize-dropdown {
	.active {
		color: $white;
		background: $primary-color;
	}

	[data-selectable] .highlight {
		background: rgba($black, 0.1);
	}
}

.selectize-dropdown,
.selectize-input,
.selectize-input input {
	font-size: rem-calc(15);
}
