// Main Style
@import 'layout/variable_mixin_function';

// --------------------------------------------------------

/* Vendor */
@import './vendor/selectize/selectize'; // 收款帳戶使用下拉選單

// --------------------------------------------------------

/* Component */
@import './components/mem-user-info';
@import './components/pd-fixed-info';
@import './components/pd-list';
@import './components/pd-list-sec'; // 為了購買紀錄詳情頁(member-record-cont.html)
@import './components/cart-detail'; // 為了購買紀錄詳情頁(member-record-cont.html)
@import './components/cart-form'; // 為了[區塊鏈]轉出
@import './components/cart-order-detail'; // 為了購買紀錄詳情頁(member-record-cont.html)
@import './components/list-small-note'; // 為了[區塊鏈]轉出

.input-box {
	margin-bottom: rem-calc(40 - 8);

	input {
		margin-bottom: rem-calc(8);
	}

	.select {
		margin-bottom: rem-calc(8);
	}

	.selectize-control {
		margin-bottom: rem-calc(8 - 5);
	}
}

.text-tip {
	font-size: rem-calc(12);
	line-height: 1.5;
	color: #666;
	position: relative;
	z-index: 10;
}

// --------------------------------------------------------

/* Container */
$mem-menu-bp-mobile: medium down;
$mem-menu-bp-large: large;

.page-mem-home {
	@include breakpoint($mem-menu-bp-mobile) {
		.mem {
			&__l {
				display: block;
			}

			&__r {
				display: none;
			}
		}
	}
}

%mem-bg {
	background-image: linear-gradient(to bottom, #f7f9fb 0%, #f7f9fb calc(100% - 50px), rgba(#f7f9fb, 0) 100%);
}

.mem {
	@extend %mem-bg;

	@include breakpoint($mem-menu-bp-mobile) {
		&__l {
			display: none;
		}
	}

	@include breakpoint($mem-menu-bp-large) {
		background-image: none;

		&__wrapper {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;
		}

		&__l {
			$width: percentage(4 / 12);

			margin-right: rem-calc(40);
			flex: 0 0 $width;
			width: $width;
		}

		&__r {
			flex: 1;
		}
	}
}

@mixin panel {
	border-radius: rem-calc(6);
	box-shadow: $shadow-blue;
	background-color: #fff;
}

%panel {
	@include panel;
}

@include breakpoint($mem-menu-bp-mobile) {
	%panel-mo {
		@include panel;
	}
}

@include breakpoint($mem-menu-bp-large) {
	%panel-large {
		@include panel;
	}
}

.mem-aside {
	@include breakpoint($mem-menu-bp-large) {
		@extend %panel-large;
	}
}

.mem-menu-main {
	@include breakpoint($mem-menu-bp-mobile) {
		@extend %panel-mo;
	}
}

// 會員資訊
.mem-info {
	border-bottom: $border-global;

	&__link {
		display: block;
		padding: rem-calc(20);

		&,
		&:hover,
		&:focus {
			color: $primary-dark;
		}

		@include breakpoint(medium) {
			padding: rem-calc(24);
		}

		@include breakpoint-hover {
			&:hover,
			&:focus {
				background: rgba($primary-color, 0.05);
			}
		}
	}

	.user-info {
		justify-content: flex-start;

		&__photo-box {
			$size: rem-calc(48);

			margin-right: rem-calc(12);
			width: $size;
			height: $size;
		}
	}
}

// 統計數量
.mem-count {
	display: flex;
	margin-bottom: rem-calc(27);

	&__cell {
		position: relative;
		flex: 1;
		padding: rem-calc(8);
		text-align: center;

		&:not(:last-child) {
			&::before {
				content: '';
				position: absolute;
				top: 50%;
				right: 0;
				width: 1px;
				height: calc(100% - #{rem-calc(15 * 2)});
				background: #ddd;
				opacity: 0.3;
				transform: translateY(-50%);
			}
		}
	}

	&__num {
		display: block;
		font-size: rem-calc(24);
		color: $primary-color;
		font-weight: $font-bold-b;
	}

	&__text {
		display: block;
		font-size: rem-calc(13);
		color: #333;
	}
}

// 選單區塊
.mem-menu {
	$root: &;
	$padding-x: rem-calc(20);

	&__block {
		margin-bottom: rem-calc(42);
	}

	&__title {
		margin-bottom: rem-calc(14);
		color: $primary-dark;

		@include breakpoint(medium) {
			margin-bottom: rem-calc(18);
			padding: 0 $padding-x;
			font-size: rem-calc(15);
			color: #666;
		}
	}

	&__cont {
		@include breakpoint($mem-menu-bp-mobile) {
			@extend %panel-mo;
		}
	}

	&__list {
		list-style: none;
		margin-left: 0;
		margin-bottom: 0;
	}

	&__item {
		overflow: hidden;

		&:not(:last-child) {
			border-bottom: $border-global;
		}

		@include breakpoint($mem-menu-bp-mobile) {
			&:first-child {
				border-top-left-radius: rem-calc(6);
				border-top-right-radius: rem-calc(6);
			}

			&:last-child {
				border-bottom-left-radius: rem-calc(6);
				border-bottom-right-radius: rem-calc(6);
			}
		}

		&.is-active {
			#{$root} {
				&__link {
					background: rgba($primary-color, 0.15);
				}
			}

			@include breakpoint-hover {
				#{$root} {
					&__link {
						&:hover,
						&:focus {
							background: rgba($primary-color, 0.15);
						}
					}
				}
			}
		}

		&.no-arrow {
			#{$root} {
				&__link {
					&::after {
						display: none;
					}
				}
			}
		}
	}

	&__link {
		position: relative;
		display: flex;
		align-items: center;
		padding: rem-calc(15) $padding-x;
		padding-left: ($padding-x - rem-calc(5));
		width: 100%;

		&,
		&:hover,
		&:focus {
			color: #666;
		}

		.be-icon {
			font-size: rem-calc(24);
			color: $primary-color;
		}

		&::after {
			@include font-icon-add(arrow-right);

			position: absolute;
			top: 50%;
			right: ($padding-x + rem-calc(4));
			margin-top: 2px;
			font-size: rem-calc(14);
			transform: translateY(-50%);
		}

		@include breakpoint-hover {
			&:hover,
			&:focus {
				background: rgba($primary-color, 0.05);
			}
		}
	}

	&__text {
		font-weight: 500;
		color: $primary-dark;
	}

	&__num {
		position: absolute;
		top: 50%;
		right: rem-calc(50);
		transform: translateY(-50%);
	}
}

// 右側內容
.mem-cont {
	.sec-title-box {
		@include breakpoint(small only) {
			align-items: flex-start;
		}

		&__title {
			@include breakpoint(medium) {
				display: inline-block;
			}
		}

		&__tip {
			margin-left: rem-calc(5);
			@include breakpoint(medium) {
				display: inline-block;
			}
		}
	}
}

// 內容空值
.mem-cont-empty {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	min-height: rem-calc(500);

	@include breakpoint(medium down) {
		// border: 1px solid #000;
		// 180: 上方空間；其餘為瀏覽器預設功能（網址、下方功能）高度
		min-height: calc(100vh - #{rem-calc(180px + 50px + 44px)});
	}

	&__img-box {
		margin-bottom: rem-calc(12);

		.be-icon {
			font-size: rem-calc(40);
			color: #ddd;

			@include breakpoint(medium) {
				font-size: rem-calc(48);
			}
		}
	}

	&__text {
		color: #666;

		margin-bottom: rem-calc(30);
		font-size: rem-calc(13);

		@include breakpoint(medium) {
			margin-bottom: rem-calc(40);
			font-size: rem-calc(15);
		}
	}

	.btn {
		margin-bottom: 0;
		padding: rem-calc(7 10);
		min-width: rem-calc(106);
	}
}

