// --------------------------------------------------------
// --------------------------------------------------------
// --------------------------------------------------------
// --------------------------------------------------------
// --------------------------------------------------------
// --------------------------------------------------------
// --------------------------------------------------------
// --------------------------------------------------------

// --------------------------------------------------------

// 賣家中心
.mem-seller-home {
	.mem-count {
		&__cell {
			&.total {
				$width: percentage(144 / 335);

				flex: 0 0 $width;
				width: $width;
			}
		}

		&__num {
			font-size: rem-calc(20);
		}
	}
}

// --------------------------------------------------------

// 會員紀錄
.mem-record {
	.sec-title-box {
		margin-bottom: rem-calc(12);
		@include breakpoint(medium) {
			margin-bottom: rem-calc(20);
		}
	}
}

// 問答紀錄
.mem-ask {
	.page-nav {
		margin-bottom: rem-calc(20);

		&__tabs-cont {
			border: 0;
			background: transparent;
		}

		&__link {
			@include breakpoint(large) {
				font-size: rem-calc(18);
			}
		}
	}
}

.mem-record,
.mem-ask {
	.mem {
		@include breakpoint($mem-menu-bp-large) {
			&__r {
				max-width: calc(66.6666% - #{rem-calc(40)});
			}
		}
	}
}

