// 實名制認證
.statue-box {
	margin-top: rem-calc(20);
	margin-bottom: rem-calc(40);

	@include breakpoint(large) {
		margin-top: rem-calc(60);
		margin-bottom: rem-calc(65);
	}

	&__cover {
		margin-right: auto;
		margin-left: auto;

		@include breakpoint(large) {
			width: rem-calc(265);
		}

		&-img {
			width: rem-calc(155);
			margin-bottom: rem-calc(24);

			@include breakpoint(medium) {
				width: rem-calc(178);
				margin-bottom: rem-calc(36);
			}
		}

		&-title {
			color: $primary-color;
			font-weight: $font-bold-b;

			@include breakpoint(small down) {
				font-size: rem-calc(20);
			}
		}

		&-txt {
			@include breakpoint(small down) {
				font-weight: normal;
			}
		}
	}

	&__title {
		margin-bottom: rem-calc(28);

		@include breakpoint(medium) {
			padding-top: rem-calc(7);
		}
	}

	&__txt {
		margin-bottom: 0;
		font-weight: normal;

		&:not(:last-child) {
			margin-bottom: rem-calc(30);

			@include breakpoint(medium down) {
				margin-bottom: rem-calc(25);
			}
		}

		.bold {
			font-weight: $font-bold-b;
		}

		.link {
			font-weight: $font-bold-m;
		}
	}
}

// app fixed 資訊
.pd-fixed-info {
	padding: rem-calc(10 20);

	.btn {
		&:not(:last-child) {
			margin-bottom: rem-calc(10);
		}
	}

	.small-note {
		margin-bottom: rem-calc(8);
	}
}

.tb-info {
	$root: &;
	font-size: rem-calc(14);

	&__item {
		display: flex;
		padding: rem-calc(17 16);

		&:not(:last-child) {
			border-bottom: $border-global;
		}
	}

	&__title {
		width: 6em;
		color: #666;
	}

	&__cont {
		flex: 1 1 0;
		font-weight: $font-bold-m;
		text-align: right;
		color: $primary-dark;
	}

	&.no-border {
		padding-top: rem-calc(16 - 4);
		padding-bottom: rem-calc(16 - 4);

		#{$root} {
			&__item {
				padding-top: rem-calc(4);
				padding-bottom: rem-calc(4);

				&:not(:last-child) {
					border-bottom: 0 none;
				}
			}
		}
	}
}

