%mem-inside-sec {
	@extend %mem-bg;

	// 180: 上方空間；其餘為瀏覽器預設功能（網址、下方功能）高度
	min-height: calc(100vh - #{rem-calc(180px + 50px + 44px)});
}

// 實名制詳細資訊
.real-name {
	@extend %mem-inside-sec;
}

// --------------------------------------------------------

// 新增帳戶內容頁
.seller-card {
	@extend %mem-inside-sec;

	&__btn-box {
		display: flex;
		margin-bottom: rem-calc(16);

		.btn {
			&,
			&:not(:last-child) {
				margin-bottom: 0;
			}
		}
	}

	&__btn-del {
		flex: 1;
		padding-right: rem-calc(4);
		padding-left: rem-calc(4);
		min-width: 0;
		margin-right: rem-calc(11);

		.be-icon {
			display: inline-block;
			font-size: rem-calc(24);
			transform: translateY(2px);
		}
	}

	&__btn-submit {
		width: calc(100% - #{rem-calc(90 + 11)}); // 寬 + margin-right （同首頁列表的重製）
	}
}

.sell-card-list {
	$root: &;

	&__item {
		&:not(:last-child) {
			margin-bottom: rem-calc(20);
		}
	}

	&__link {
		display: block;
		padding: rem-calc(20);
		padding-bottom: rem-calc(20 + 7);
	}

	&__title-box {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin-bottom: rem-calc(40);
		color: #666;
	}

	&__bank-name {
		font-size: rem-calc(14);
	}

	&__account {
		color: $primary-dark;
		font-size: rem-calc(24);
		font-weight: $font-bold-b;

		.pixelate {
			display: inline-block;
			margin-right: 0.3em;
			font-family: 'icon', sans-serif;
			font-size: percentage(32 / 24);
			line-height: 1;
			transform: translateY(25%);
		}
	}

	&__item.add {
		#{$root} {
			&__link {
				display: flex;
				align-items: center;
				justify-content: center;
				min-height: rem-calc(144);
				background-color: #f4f4f4;
				border: solid 1px #eee;
				border-radius: rem-calc(12);
				font-size: rem-calc(13);

				.be-icon {
					display: inline-block;
					font-size: percentage(18 / 13);
				}
			}
		}
	}
}

/* Dropdown 套件 */
.selectize-control.select {
	padding: 0;
	background: none;
	height: auto;
	border: 0 none;
}

.selectize-dropdown.select {
	height: auto;
	padding: 0;
}

