// 提領收益
.seller-income {
	@extend %mem-inside-sec;
}

// 提領收益資訊
.seller-income-info {
	margin-bottom: rem-calc(40);

	&__main-box {
		padding: rem-calc(20 16 26);
		border-bottom: $border-global;

		.sec-title-box {
			margin-bottom: rem-calc(22);
		}

		.text-arrow {
			color: #999;
		}
	}

	&__money-box {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		font-size: rem-calc(20);
		font-weight: $font-bold-b;
		color: $primary-color;
	}

	&__money {
		font-size: percentage(28 / 20);
	}
}

// 提領紀錄 (Tab)
.income-his {
	.tabs-panel {
		padding-top: rem-calc(20);
	}

	&__empty {
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: calc(100vh - #{rem-calc(89 + 168)}); // 44 + 45
	}
}

// 提領紀錄 (Accordion)
.income-his-info {
	$root: &;

	@at-root %income-block {
		display: flex;
		justify-content: space-between;
	}

	&__top {
		@extend %income-block;

		margin-bottom: rem-calc(12);
	}

	&__bottom {
		@extend %income-block;

		align-items: center;
		padding-right: rem-calc(24);
	}

	&__money-box {
		margin-bottom: 0;
	}

	// 提領細項（展開後內容）
	&__detail {
		margin-bottom: 0;

		&__item {
			display: flex;
			justify-content: space-between;
			font-size: rem-calc(12);
			color: #666;

			&:not(:last-child) {
				margin-bottom: rem-calc(10);
			}
		}
	}

	.accordion-item {
		border-top: $border-global;

		// 展開
		&.is-active {
			.accordion-title {
				.be-icon {
					&::before {
						transform: rotate(180deg);
					}
				}
			}
		}
	}

	.accordion-title {
		padding: rem-calc(18 0 11);
		color: $primary-dark;
		background: transparent;
		border: 0 none !important;

		&::before {
			display: none;
		}

		.be-icon {
			position: absolute;
			bottom: rem-calc(18);
			right: 0;
		}

		#{$root} {
			&__top {
				color: #666;
			}
		}
	}

	.accordion-content {
		padding: rem-calc(0 0 20);
		border: 0 none !important;
	}
}

