// D.7 銷售清單
.seller-solid {
	.pd-card__list-info {
		margin-top: rem-calc(8);

		&__item {
			&:not(:last-child) {
				margin-bottom: rem-calc(2);
			}

			&:last-child {
				margin-top: rem-calc(4);
				font-size: percentage(14/12);
			}

			&:not(.highlight) {
				.pd-card__list-info {
					&__title {
						font-weight: normal;
					}
				}
			}
		}

		&.space-between {
			.pd-card__list-info {
				&__title {
					width: 7em;
				}

				&__cont {
					width: calc(100% - 7em);
				}

				@include breakpoint($xs) {
					&__title {
						width: 8em;
					}

					&__cont {
						width: calc(100% - 8em);
					}
				}
			}
		}
	}
}

