// [區塊鏈] 轉出
.blockchain-out {
	.pd-card {
		margin-bottom: rem-calc(32);
	}

	.input-box {
		margin-bottom: rem-calc(40);

		&.mb-0 {
			margin-bottom: 0;
		}
	}

	.cart-form {
		@include breakpoint(large) {
			margin-bottom: rem-calc(40);
		}
	}

	.currency.back {
		color: #999;
	}
}

.blockchain-out-detail {
	&__item {
		margin-bottom: rem-calc(20);
	}

	.text-tip {
		margin-top: rem-calc(10);
	}
}

.text-space-between {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

// --------------------------------------------------------

// 付款完成 - 商品詳細資料
.blockchain-detail {
	.pd-card__link {
		padding-bottom: rem-calc(15);
	}

	.pd-card__list-info {
		padding: rem-calc(0 20 20);
		color: #666;

		&__item {
			&:not(:last-child) {
				margin-bottom: rem-calc(2);
			}
		}

		&__title {
			font-weight: normal;
		}
	}

	.be-icon-external-link {
		&::before {
			transform: scale(#{(18/12)});
			transform-origin: left center;
		}
	}
}

