// [區塊鏈] 轉入
@function strip-units($number) {
	@return $number / ($number * 0 + 1);
}

@function fluid-strip-unit($value) {
	@return $value / ($value * 0 + 1);
}

.blockchain-in {
	display: flex;
	align-items: center;
	text-align: center;

	// 180: 上方空間；其餘為瀏覽器預設功能（網址、下方功能）高度
	min-height: calc(100vh - #{rem-calc(180px + 50px + 44px)});

	&__cont {
		margin: 0 auto;
		width: 100%;
		max-width: rem-calc(480);
	}

	&__title {
		margin-bottom: rem-calc(24);
	}

	&__list {
		margin-bottom: 0;
	}

	&__item {
		width: 100%;
		text-align: center;

		&:not(:last-child) {
			margin-bottom: rem-calc(20);
		}
	}

	&__btn {
		display: block;
		padding: rem-calc(16);
		width: 100%;
		min-width: 0;

		img {
			max-height: rem-calc(64);
		}
	}
}
